
export const setSeason = (season) => ({
    type: "SET_SEASON",
    season
});

export const setHeadliner = (headliner) => ({
    type: "SET_HEADLINER",
    headliner
});

export const setGuest = (guest) => ({
    type: "SET_GUEST",
    guest
});

export const setSelectBy = (selectBy) => ({
    type: "SET_SELECT_BY",
    selectBy
});


export const clearCriteria = () => ({
    type: "CLEAR_CRITERIA"
});








